import { Component, OnInit } from '@angular/core';
import { PlayerService } from '../shared/services/player.service';
import * as Marquee from 'src/app/streamer/marquee'

@Component({
  selector: 'streamer',
  templateUrl: './streamer.component.html',
  styleUrls: ['./streamer.component.css'],
})
export class StreamerComponent implements OnInit {
  hour: string;

  constructor(private playerService: PlayerService) {
    setInterval(() => {
      this.hour = new Date().toString().split(' ')[4].substr(0, 5);
    }, 1);
  }

  ngOnInit(): void {
    Marquee.initializeMarquee();
  }

  public get playing(): boolean {
    return this.playerService.playing;
  }

  public toggleTuneIn() {
    this.playerService.toggleTuneIn();
  }
}
