import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class PlayerService {
  hasTunedIn: boolean = false;
  playing: boolean = false;

  constructor() { }

  public toggleTuneIn() {
    if (!this.hasTunedIn) this.hasTunedIn = true;

    if (this.playing) this.playing = false
    else this.playing = true;
  }

  public tuneIn() {
    if (!this.hasTunedIn) this.toggleTuneIn();
  }
}
